import { Component, OnInit } from '@angular/core';
import projectJSONFile from '../../../assets/projectsJSON.json';
import * as AOS from 'aos';

@Component({
  selector: 'app-projects',
  templateUrl: './projects.component.html',
  styleUrls: ['./projects.component.scss']
})
export class ProjectsComponent implements OnInit {
  projectJSON: any;
  constructor() {
    let i = 0; 
    let temp = [];
    this.projectJSON = []
    for( let project of projectJSONFile.projects){
      if (i != 0  && i%2 == 0){
        this.projectJSON.push(temp);
        temp = []
      } 
      temp.push(project);
      i++;
    }
    this.projectJSON.push(temp);

    console.log(this.projectJSON);
  }

  ngOnInit() {
    // this.projectJSON = projectJSONFile.projects;
    AOS.init();
  }

  redirectToProject(url){
      console.log(url);
      let win = window.open(url, '_blank');
      win.focus();
  }

}
