import { Component, OnInit, ViewChild, ElementRef } from '@angular/core';
import Typed  from 'typed.js';
import { faLinkedin }  from  '@fortawesome/fontawesome-free'


declare var self: any


@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.scss','../../../../node_modules/@fortawesome/fontawesome-free/css/all.min.css']
})

export class HomeComponent implements OnInit {
  
  faLinkedin = faLinkedin;
  @ViewChild('story', {static: true}) story: ElementRef;
  constructor() {
    
   }
  ngOnInit() {
    var story = document.getElementById('story');
    self = this;
    self.userName = "Sarthak Dadhakar";
    self.callMe()

  }
  callMe(){
    document.getElementById('story').innerHTML = '';
    var typed = new Typed(this.story.nativeElement, {
      strings: [
        "Android Development", "Web Development","Machine Learning","Deep Learning","Natural Language Processing",""
      ],
      typeSpeed: 120,
      loop:true,
      cursorChar: '_',
      onComplete(){
          document.getElementById('story').innerHTML = '';
          
        }
    
    });
  }
}
